import React from "react";
import PageCardWrapper from "./PageCardWrapper";
import { TextField, Select, MenuItem, InputLabel, FormControl, Grid } from "@mui/material";
import LoadingButton from '@mui/lab/LoadingButton';
import SaveIcon from '@mui/icons-material/Save';
import { useForm } from "react-hook-form";
import { AddRecord } from '../Utils/Servies'
function Add() {
  const { register, formState: { errors }, handleSubmit } = useForm();
  const onSubmit = (data: any) => { 
    setLoadingState(true);
    AddRecord({});
    setTimeout(()=> { setLoadingState(false)}, 5000)
  };
  const [loadingState, setLoadingState] = React.useState(false);
  const [cls, setCls] = React.useState('');
  const [sec, setSec] = React.useState('');

  const handleClassChange = (event: any) => {
    setCls(event.target.value);
  };

  const handleSectionChange = (event: any) => {
    setSec(event.target.value);
  };

  return (<>
    <PageCardWrapper Element=
      {
        <Grid container justifyContent="center" alignItems='center'>
          <Grid style={{marginBottom: 20}}><h1>Add Student Record</h1><form style={{ marginLeft: 20 }} onSubmit={handleSubmit(onSubmit)}>
            <TextField
              label="ID"
              variant="standard"
              inputProps={
                { ...register("id", { required: true }) }
              }
              sx={{  minWidth: { xs: 50, sm: 100, md: 200, lg: 300} }}
            /><br />
            <p style={{ color: "red" }}>{errors.id && "ID is required"} </p><br />
            <TextField
              label="Full Name"
              variant="standard"
              inputProps={
                { ...register("name", { required: true }) }
              }
              sx={{  minWidth: { xs: 50, sm: 100, md: 200, lg: 300} }}
            /><br />
            <p style={{ color: "red" }}>{errors.name && "Name is required"}</p> <br />

            <TextField
              label="Age"
              variant="standard"
              inputProps={
                { ...register("age", { required: true }), type: "number" }
              }
              sx={{  minWidth: { xs: 50, sm: 100, md: 200, lg: 300} }}
            /><br />
            <p style={{ color: "red" }}>{errors.name && "Age is required"}</p> <br />
            <FormControl variant="standard" sx={{  minWidth: { xs: 100, sm: 100, md: 120, lg: 120} }}>
              <InputLabel id="add-form-class">Class</InputLabel>
              <Select
                labelId="add-form-class"
                id="add-form-class"
                value={cls}
                onChange={handleClassChange}
                inputProps={
                  { ...register("class", { required: true }) }
                }
                label="Class"
              >
                <MenuItem value={1}>1th</MenuItem>
                <MenuItem value={2}>2nd</MenuItem>
                <MenuItem value={3}>3rd</MenuItem>
                <MenuItem value={4}>4th</MenuItem>
                <MenuItem value={5}>5th</MenuItem>
                <MenuItem value={6}>6th</MenuItem>
                <MenuItem value={7}>7th</MenuItem>
                <MenuItem value={8}>8th</MenuItem>
                <MenuItem value={9}>9th</MenuItem>
                <MenuItem value={10}>10th</MenuItem>
              </Select>
            </FormControl><br />
            <p style={{ color: "red" }}>{errors.class && "class is required"}</p> <br />
            <FormControl variant="standard" sx={{  minWidth: { xs: 100, sm: 100, md: 120, lg: 120} }}>
              <InputLabel id="add-form-sec">Section</InputLabel>
              <Select
                labelId="add-form-sec"
                id="add-form-sec"
                value={sec}
                onChange={handleSectionChange}
                inputProps={
                  { ...register("section", { required: true }) }
                }
                label="section"
              >
                <MenuItem value={"A"}>A</MenuItem>
                <MenuItem value={"B"}>B</MenuItem>
                <MenuItem value={"C"}>C</MenuItem>
                <MenuItem value={"D"}>D</MenuItem>
              </Select>
            </FormControl> <br />
            <p style={{ color: "red" }}>{errors.section && "section is required"}</p> <br />
            <LoadingButton
              loading={loadingState}
              loadingPosition="start"
              startIcon={<SaveIcon />}
              variant="contained"
              type="submit"
            >
              Save
            </LoadingButton>
          </form></Grid>
        </Grid>
      }></PageCardWrapper>
  </>)
}

export default Add;